$primary_color: #399CFF;
$primary_color_100: #D7EBFF;
$primary_color_300: #88C4FF;
$neutral_color_50: #F7F7F7;
$neutral_color_100: #E1E3E8;
$neutral_color_200: #C3C8D2;
$neutral_color_300: #A6ACBB;
$secondary_color: #C3C8D2;
$background_color: #D7EBFF;
$white_color: #ffffff;
$heading_color: #0B0C0E;
$primary_font_family: "Plus Jakarta Sans", sans-serif;
$secondary_font_family: "Work Sans", sans-serif;
$tiny_font: 12px;
$small_font: 14px;
$tiny_line_height: 14px;
$small_line_height: 16px;


/** Start Dual Button Scss **/
.fb-dual-button-container{
	.fb-dual-button-wrapper {
		display: flex;
        .fb-advanced-button{
            height: 100%;
        }
	}
	.fb-dual-button-connector{
		position: relative;
	}
	.fb-dual-button-connector-text{
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		z-index: 99;
		border-radius: 50px;
		text-align: center;
        width: 35px;
        height: 35px;
        line-height: 35px;
	}
    @media all and (max-width: 1024px) {
        .fb-dual-button-connector-text{
            width: fit-content;
            line-height: 1.3;
            height: 15px;
            padding: 5px;
        }
    }
    @media all and (max-width: 767px) {
        .fb-dual-button-connector-text{
            width: fit-content;
            line-height: 1.3;
            height: 15px;
            padding: 5px;
        }
    }
}

/** Start General scss for Dual Button **/
.fb-advanced-button-icon-popup {
    padding: 0.2rem;
    min-width: 220px;
    .fb-advanced-button-icon-popup-inner {
        position: relative;
        z-index: 99999;
        .components-base-control__field {
            margin-bottom: 0;
            label {
                display: block;
                color: $secondary_color !important;
                font-size: 12px !important;
                font-weight: 500 !important;
                line-height: 16px !important;
                margin: 10px 0 !important;
                min-height: auto;
                text-transform: capitalize !important;
            }
            .components-toggle-control {
                margin-bottom: 0 !important;
            }
        }
    }
}

.fb-link-control-wrapper {
    .fb-link-control-inner-wrapper {
        display: flex;
        align-items: center;
        gap: 10px;
        .fb-advanced-button-url {
            flex: 1;
            margin-bottom: 0;
            .components-base-control__field {
                margin-bottom: 0;
            }
        }
    }
}
/** End General scss for Dual Button **/

.fb-dual-button-container {
    .fb-advanced-button {
        align-items: center;
        display: inline-flex;
        justify-content: center;
        outline: none;
        text-align: center;
        text-decoration: none;
        padding: 12px 30px;
        background-color: #3f8efc;
        border-radius: 0px;
        box-sizing: border-box;
        cursor: pointer;
        position: relative;
        transition: all .3s ease-in-out;
        height: 100%;
        white-space: nowrap;
        .fb-advanced-button-text {
            color: #ffffff;
            transition: all .3s ease-in-out;
        }
    }
	.fb-dual-button-wrapper {
		display: flex;
	}
	.fb-dual-button-connector{
		position: relative;
	}
	.fb-dual-button-connector-text{
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		z-index: 99;
		border-radius: 50px;
		text-align: center;
        width: 35px;
        height: 35px;
        line-height: 35px;
	}
    @media all and (max-width: 1024px) {
        .fb-dual-button-connector-text{
            width: fit-content;
            line-height: 1.3;
            height: 15px;
        }
    }
    @media all and (max-width: 767px) {
        .fb-dual-button-connector-text{
            width: fit-content;
            line-height: 1.3;
            height: 15px;
        }
    }
    .fb-advanced-button-icon {
        display: block;
        position: relative;
        z-index: 1;
        svg {
            display: block;
            height: auto;
            width: 16px;
            fill: #fff;
            box-sizing: content-box;
        }
    }
}


